.jumbotron {
	background: -webkit-gradient(linear, left top, right top, from(rgba(232,207,132,0)), to(rgba(232,207,132,0.5)));
	background: -webkit-linear-gradient(left, rgba(232,207,132,0), rgba(232,207,132,0.5));
	background: -o-linear-gradient(left, rgba(232,207,132,0), rgba(232,207,132,0.5));
	background: linear-gradient(to right, rgba(232,207,132,0), rgba(232,207,132,0.5));
	color: #900C3E;
}


